<template>
    <div class="swiper_container_1 ">
	    <div class="swiper-wrapper">
	        <div class="swiper-slide" 
                v-for='item of banner'
                :key='item._id'>
                <router-link 
                :to="'/detail/'+item._id+'?dataName=banner'"
                > 
                    <img :src="baseUrl+item.detail.auth_icon">
                </router-link>
            </div>
	        
	    </div>
	    <!-- Add Pagination -->
	    <div class="swiper_pagination_1"></div>
	</div> 
</template>

<script>
import Swiper from 'swiper'
    export default {
        props:['banner'],
        updated(){
            new Swiper('.swiper_container_1', {
                pagination: {el:'.swiper_pagination_1'},
                autoplay : {disableOnInteraction: false}
            });
        }
    }
</script>

