import Vue from 'vue'
import App from './App.vue'
import router from './plugins/router.config.js'
import Vant from 'vant'

Vue.use(Vant)

import store from './plugins/store'

Vue.config.productionTip = false

import './assets/css/vant.css'
import $ from 'jquery'
import Swiper from 'swipe'
import './assets/css/index.css'
import './assets/css/zy.css'
import './assets/css/swiper.min.css'
import axios from  './plugins/axios';

import 'animate.css'

let local = window.localStorage.getItem('vuex_user');
if(local){
  store.commit('UPDATE_USER',JSON.parse(local))
}

let local2 = window.localStorage.getItem('buycar_item');
if(local2){
  store.commit('ADD_ITEM',JSON.parse(local2))
}

Vue.prototype.baseUrl = 'http://114.55.146.235:80'

new Vue({
  render: h => h(App),
  router,store
}).$mount('#app')
