<template>
    <div>
        <div class="toub_beij toub_beij_zy">
            <div class="zhongj_k zhongj_k_z">
                <div class="jiu_feo">
                    <div class="logo_k logo_k_fanh"><a href="javascript:window.history.go(-1)"><img class="fanh_ann" src="../assets/images/web/fanh.png"></a></div>
                    <div class="zhongjain_daoh">
                        <ul>
                            <li><h1 class="dllogo"><img src="../assets/images/web/logo.jpg"></h1><a class="denglxil">欢迎注册</a></li>  
                        </ul>
                    </div>
                </div> 
            </div>
        </div>
        
        <!--内容-->
        <div class="zhuc_qieh">
            <div class="zhuc"><a href="wangid_ZhuCe.html">注册账号</a></div>
            <div><a href="wangid_RuZhu.html">申请入驻</a></div>
        </div>
        <div class="denglu_k">
            <div class="denglk denglk_l">
                <div class="mz px24">用户名：</div>
                <input type="text" placeholder="" v-model='username'>
            </div>
            <div class="denglk denglk_l">
                <div class="mz px24">设置密码：</div>
                <input type="text" placeholder="" v-model='password'>
            </div>
            <div><input type="file" ref='f1'></div>
           <div>{{mess}}</div>
            
            <div class="dengl_anniu"><a href="javascript:;" @click='reg'>提 交</a></div> 
            <div class="wangjimm wangjimm_1">
                <p class="px22">
                    <input type="checkbox" name="Car" class="px22">
                    阅读并同意                                              
                    <a href="#">《wangdi通城用户注册协议》</a>
                    <a href="#">《隐私协议》</a>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
export default {
    data(){
        return {
            username:'',
            password:'',
            mess:''
        }
    },
    methods:{
        reg(){
            let formData = new FormData();
            formData.append('username',this.username)
            formData.append('password',this.password)
            formData.append('icon',this.$refs.f1.files[0])
            axios({
                url:'/api/reg',
                method:'post',
                data:formData
            }).then(
                res=>res.data.err===0?this.$router.push('/login'):this.mess=res.data.msg
            )
        }
    },
    mounted(){
        //滑动头部透明（针对首页头部用）
        window.onscroll=function(){

        var autoheight=document.body.scrollTop||document.documentElement.scrollTop;
        if(autoheight>10){
            $(".toub_beij").css("position" ,"fixed")
            }else{
                $(".toub_beij").css("position" ,"relative")
            }
        }
    }
}
</script>