<template>
    <div class='shopcar'>
        <div class="toub_beij toub_beij_zy">
	<div class="zhongj_k zhongj_k_z">
		<div class="jiu_feo">
			<div class="logo_k logo_k_fanh"><a  href="javascript:window.history.go(-1)"><img class="fanh_ann" src="../assets/images/web/fanh.png"></a></div>
			<div class="zhongjain_daoh">
				<ul>
					<li><a class="zyyge">购物车</a></li>  
				</ul>
			</div>
		</div>
		<div class="dengl">
			<div class="caid_img" id="zhu_caid"><img src="../assets/images/web/cd_1.png"></div>
			
		</div>
	</div>
</div>

<!--内容--> 
<div v-if='bl'>
	<div class="gouwuc_kk">
		<ul>
			<li>
				<div
				class="diannao_shanp"
				v-for='item of buycar'
				:key='item._id'
				>
					<i class='i1'><input type='checkbox' ref='check' v-model='item.flag'></i>
					<a href="XiangQ_Y.html"><h1><img :src="`${baseUrl}${item.icon}`"></h1></a>
					<div class="you">
						<a href="XiangQ_Y.html"><p class="px24 bt">{{item.des}}</p></a>
						<p class="xh">特惠装</p>
						<div class="youjfy">
							<div class="goum_sl">
								<p class="px26 jiaq">{{item.jia}}</p>
								<div class="counter_box">
									<i class="jian" @click="change({_id:item._id,num:-1})">-</i>
									<input type="text" v-model='item.number' readonly>
									<i class="jia" @click="change({_id:item._id,num:1})">+</i>
								</div>
							</div>
						</div>
						<div class="bianj">
							<a href="javascript:;" class="ayou" @click="remove({_id:item._id})"> <p><i class="sc"></i>删除</p></a>
						</div>
					</div>
				</div>
			</li>
		</ul>
	</div>
	<!--底部导航-->
	<!---->
	<div class="dib_daoh_b">
		<div class="gouwuc_ gouwuc_yem">
			<div class="zuo">
				<i class="i1"><p><input type='checkbox' ref='check1' @click='checkAll' v-model='hehe'></p></i> 
			</div>
			
			<div class="zhong">
				<p>
				<a href="javascript:;" @click='clear' class='enen'>清空购物车</a>

					总计：<em>￥{{count}}</em></p></div>
			<div class="you">
				<div class="goum">
					<a href="javascript:;">去结算<em>{{lala}}</em></a>  
				</div>
			</div>
		</div>
	</div>
</div>

<div v-else>
	<p>您还未购买商品,前往首页选购商品</p>
</div>

</div>
</template>

<script>
import Vue from 'vue'
import { Toast } from 'vant';
import $ from 'jquery'
import {mapGetters,mapActions} from 'vuex'
Vue.use(Toast);
export default {
	data(){
		return {
			bl:false
		}
	},
	watch:{
		buycar:{
			handler(newc,oldc){
				if(newc.length>0){
					this.bl=true
				}else{
					this.bl=false
				}
			},
			immediate:true,
			deep:true
		}
	},
	computed:{
		...mapGetters(['buycar']),
		count(){
			let result = 0;
			this.buycar.forEach(item=>{
				if(item.flag){
					result += item.jia.slice(1) * item.number
				}
			})
			return result.toFixed(1);
		},
		lala(){
			let sum = 0;
			this.buycar.forEach(item=>{
				if(item.flag){
					sum += item.number
				}
			})
			return sum;
		},
		hehe(){
			let a = false;
			a = this.buycar.some(item=>{
				if(!item.flag){
					return true
				}
			})

			if(a){
				return false;
			}else{
				return true;
			}
		}
	},
	methods:{
			change(options){
				let arr=[...this.$store.state.buycar];
				arr.forEach(item=>{
				if(item._id==options._id){
					if(item.number==1&&options.num<0) {
						Toast('不能再减少了！')
						return;
					}
					item.number+=options.num;
				}
				});
				this.$store.commit('CHANGE_ITEM',arr)
				localStorage.setItem('buycar_item',JSON.stringify(this.$store.state.buycar));
			},
			remove(options){
				let arr=[...this.$store.state.buycar];
				arr.forEach((item,index)=>{
				if(item._id==options._id){
					arr.splice(index,1);
				}
				});
				this.$store.commit('REMOVE_ITEM',arr);
				localStorage.setItem('buycar_item',JSON.stringify(this.$store.state.buycar));

			},

			clear(){
				this.$store.commit('CLEAR_BUYCAR',[]);
				localStorage.setItem('buycar_item',JSON.stringify(this.$store.state.buycar));
			},

			checkAll(){
				if(this.$refs.check1.checked){
					this.buycar.forEach(item=>{
						item.flag=true
					})
				}else{
					this.buycar.forEach(item=>{
						item.flag=false
					})
				}
			}
		
        
		
	},
	activated() {
		this.$refs.check1.checked=false;
	},
    mounted(){

		
		
        //滑动头部透明（针对首页头部用）
        window.onscroll=function(){

        var autoheight=document.body.scrollTop||document.documentElement.scrollTop;
        if(autoheight>10){
            $(".toub_beij").css("position" ,"fixed")
            }else{
                $(".toub_beij").css("position" ,"relative")
            }
        }
    }
}
</script>