<template>
  <div id="app">
    <Loading v-show='bLoading'></Loading>
    <AppHeader v-show='bNav'></AppHeader>
    
    <transition
      enter-active-class="animated bounceInLeft delay-1s"
      leave-active-class="animated bounceOutRight"
    >

      <keep-alive :exclude="['Detail']">

        <router-view></router-view>

      </keep-alive>

    </transition>
    
    <AppFooter v-show='bFoot'></AppFooter>
  </div>
</template>

<script>
import Loading from './common/Loading'
import AppHeader from './common/AppHeader'
import AppFooter from './common/AppFooter'
import Home from './components/Home'
import Liebiao from './components/Liebiao'
import ShopCar from './components/ShopCar'
import Detail from './components/Detail.vue'
import User from './components/User.vue'
import Login from './components/Login.vue'
import Reg from './components/Reg.vue'

import {mapGetters} from 'vuex'


export default {
  name: 'app',
  components: {
    AppHeader,AppFooter,Liebiao,ShopCar,Detail,User,Login,Reg,Loading
  },
  computed:mapGetters(['bNav','bFoot','bLoading']),
  watch:{
    $route:{
      handler({path}){
        if(/home|liebiao/.test(path)){
          this.$store.dispatch('VIEW_NAV',true)
          this.$store.dispatch('VIEW_FOOT',true)
        }
        if(/user/.test(path)){
          this.$store.dispatch('VIEW_NAV',false)
          this.$store.dispatch('VIEW_FOOT',true)
        }
        if(/login|reg|shopcar/.test(path)){
          this.$store.dispatch('VIEW_NAV',false)
          this.$store.dispatch('VIEW_FOOT',false)
        }
        if(path.includes("detail")){
          this.$store.dispatch('VIEW_NAV',false)
          this.$store.dispatch('VIEW_FOOT',false)
        }

      },
    immediate:true
    }
  }
  
}


</script>
