<template>
<div>
    <div class="toub_beij toub_beij_zy">
	<div class="zhongj_k zhongj_k_z">
		<div class="jiu_feo">
			<div class="logo_k logo_k_fanh"><a href="javascript:window.history.go(-1)"><img class="fanh_ann" src="images/web/fanh.png"></a></div>
			<div class="zhongjain_daoh">
				<ul>
					<li><h1 class="dllogo"><img src="images/web/logo.jpg"></h1><a class="denglxil">欢迎登录</a></li>  
				</ul>
			</div>
		</div> 
	</div>
</div>

<!--内容--> 
<div class="denglu_k">
	<div class="denglk">
		<div class="mz px24" >账号：</div>
		<input type="text" placeholder="手机号/邮箱/用户名" v-model='username'>
	</div>
	<div class="denglk">
		<div class="mz px24">密码：</div>
		<input type="text" placeholder="请输入密码" v-model='password'>
	</div>
	<div>{{mess}}</div>
	<div class="dengl_anniu"><a href="javascript:;" @click='login'>登 录</a></div>
	<div class="dengl_anniu"><router-link to='/reg' class="zc">立即注册</router-link></div>
	<div class="wangjimm">
		<a href="#">名榜授权登录！</a>
		<a href="#" class="you">忘记密码？</a>
	</div>
</div>
</div>

</template>

<script>
import $ from "jquery"
    export default {
		data(){
			return {
				username:'',
				password:'',
				mess:''
			}
		},
		methods:{
			login(){
				axios({
					url:'/api/login',
					method:'post',
					data:{username:this.username,password:this.password}
				}).then(
					res=>{
						this.$store.commit('UPDATE_USER',res.data)
						localStorage.setItem('vuex_user',JSON.stringify(res.data))
						if(res.data.err==0){
							this.$router.push('/user')
						}else{
							this.mess = res.data.msg
						}
					}
				)
			}
		},
        mounted(){
            //滑动头部透明（针对首页头部用）
            window.onscroll=function(){

            var autoheight=document.body.scrollTop||document.documentElement.scrollTop;
            if(autoheight>10){
                $(".toub_beij").css("position" ,"fixed")
                }else{
                    $(".toub_beij").css("position" ,"relative")
                }
            }
        }
    }
</script>